.navbar{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 30px;
    border-bottom: 1px solid white;
}
.navbar > h1 {
    font-size: 30px;
    color: yellow;
}
.navbar_links {
    display: flex;
    align-items: center;
}
.navbar_links > a {
    display: flex;
    align-items: center;
    font-size: 20px;
    font-weight: 500;
    color: white;
    text-decoration: none;
    padding: 0 15px;
}
.navbar_emoji {
    width: 25px;
    height: 25px;
    margin-left: 7px;
}
.star_emoji {
    width:10px;
    height:10px;
    margin-left: 7px;
}
